'use client';

import { useLocale, useTranslations } from 'next-intl';
import Link from 'next/link';
import Image from 'next/image';
import { usePathname, useRouter } from 'next/navigation';
import { ChangeEvent, useTransition, useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Menu, X, Landmark, Compass, Map, Image as ImageIcon, Phone } from 'lucide-react';
import { siteInfo } from '@/data/siteInfo';

export default function Header() {
    const t = useTranslations('Header');
    const locale = useLocale();
    const router = useRouter();
    const pathname = usePathname();
    const [isPending, startTransition] = useTransition();
    const [isScrolled, setIsScrolled] = useState(false);
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 20);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    useEffect(() => {
        if (isMenuOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'unset';
        }
    }, [isMenuOpen]);

    const handleLocaleChange = (e: ChangeEvent<HTMLSelectElement>) => {
        const nextLocale = e.target.value;
        startTransition(() => {
            const newPath = pathname.replace(`/${locale}`, `/${nextLocale}`);
            router.replace(newPath);
        });
    };

    const navLinks = [
        { href: `/${locale}`, label: 'Home', icon: Compass },
        { href: `/${locale}/tours`, label: 'Adventures', icon: Map },
        { href: `/${locale}/about`, label: 'About', icon: Landmark },
        { href: `/${locale}/gallery`, label: 'Gallery', icon: ImageIcon },
        { href: `/${locale}/contact`, label: 'Contact', icon: Phone },
    ];

    const isHomePage = pathname === `/${locale}`;
    const isToursPage = pathname.startsWith(`/${locale}/tours`);
    const isToursListing = pathname === `/${locale}/tours`;

    // Pages that have dark backgrounds/images at top
    const needsWhiteTextOnStart = isHomePage || isToursPage;

    const isDarkText = isScrolled || !needsWhiteTextOnStart || isMenuOpen;
    const textColorClass = isDarkText ? 'text-neutral-dark' : 'text-white';
    const navTextClass = isDarkText ? 'text-neutral-dark hover:text-primary' : 'text-white/80 hover:text-white';

    const buttonClass = isDarkText
        ? 'bg-primary text-white hover:bg-primary-dark shadow-xl shadow-primary/20'
        : 'bg-white/10 backdrop-blur-md text-white border border-white/20 hover:bg-white/20';

    return (
        <>
            <header
                className={`fixed top-0 left-0 right-0 z-[60] transition-all duration-500 ${isScrolled
                    ? 'py-4 bg-white/80 backdrop-blur-xl shadow-2xl shadow-black/5 border-b border-neutral-100'
                    : 'py-8 bg-transparent'
                    }`}
            >
                <div className="container mx-auto px-6 flex items-center justify-between">

                    {/* Logo Section */}
                    <Link href={`/${locale}`} className="relative z-[70] flex items-center gap-3 group">
                        <div className={`w-12 h-12 rounded-2xl flex items-center justify-center transition-all duration-500 scale-90 md:scale-100 ${isScrolled ? 'bg-primary shadow-lg shadow-primary/20' : 'bg-white/10 backdrop-blur-md'}`}>
                            <Image
                                src="/images/logo.png"
                                alt={siteInfo.name}
                                width={32}
                                height={32}
                                className="object-contain invert brightness-0"
                            />
                        </div>
                        <span className={`text-2xl font-bold font-outfit tracking-tighter transition-colors duration-500 hidden sm:block ${textColorClass}`}>
                            {siteInfo.name.split(' ')[0]} <span className="text-primary tracking-normal">{siteInfo.name.split(' ').slice(1).join(' ')}</span>
                        </span>
                    </Link>

                    {/* Desktop Navigation */}
                    <nav className="hidden lg:flex items-center gap-10">
                        {navLinks.map((link) => (
                            <Link
                                key={link.href}
                                href={link.href}
                                className={`font-bold transition-all text-sm uppercase tracking-widest relative group px-2 py-1 ${navTextClass}`}
                            >
                                {link.label}
                                <span className="absolute bottom-0 left-0 w-0 h-0.5 bg-primary transition-all group-hover:w-full" />
                            </Link>
                        ))}
                    </nav>

                    {/* Right Actions */}
                    <div className="flex items-center gap-6 relative z-[70]">
                        <div className="hidden md:flex items-center gap-4">
                            <select
                                defaultValue={locale}
                                onChange={handleLocaleChange}
                                disabled={isPending}
                                className={`bg-transparent border-0 text-sm font-bold focus:outline-none cursor-pointer tracking-widest ${textColorClass}`}
                            >
                                <option value="en" className="text-neutral-dark">EN</option>
                                <option value="fr" className="text-neutral-dark">FR</option>
                            </select>
                        </div>

                        <Link
                            href={`/${locale}/tours`}
                            className={`hidden md:flex items-center gap-3 px-8 py-3.5 rounded-2xl font-bold text-sm tracking-widest transition-all transform hover:scale-105 active:scale-95 ${buttonClass}`}
                        >
                            BOOK NOW
                        </Link>

                        {/* Mobile Toggle */}
                        <button
                            onClick={() => setIsMenuOpen(!isMenuOpen)}
                            className={`lg:hidden w-12 h-12 rounded-2xl flex items-center justify-center transition-all ${isScrolled ? 'bg-neutral-100 text-neutral-dark' : 'bg-white/10 backdrop-blur-md text-white'}`}
                        >
                            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
                        </button>
                    </div>
                </div>
            </header>

            {/* Premium Mobile Menu */}
            <AnimatePresence>
                {isMenuOpen && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="fixed inset-0 z-[50] bg-white md:hidden"
                    >
                        <div className="h-full flex flex-col pt-32 pb-12 px-8 overflow-y-auto">
                            {/* Menu Links */}
                            <div className="flex-1 space-y-4">
                                {navLinks.map((link, idx) => (
                                    <motion.div
                                        key={link.href}
                                        initial={{ opacity: 0, x: -30 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        transition={{ delay: idx * 0.1 }}
                                    >
                                        <Link
                                            href={link.href}
                                            onClick={() => setIsMenuOpen(false)}
                                            className="flex items-center gap-6 group"
                                        >
                                            <div className="w-14 h-14 rounded-[1.5rem] bg-neutral-50 flex items-center justify-center group-hover:bg-primary/10 transition-colors">
                                                <link.icon className="w-6 h-6 text-neutral-dark group-hover:text-primary transition-colors" />
                                            </div>
                                            <span className="text-4xl font-bold font-outfit text-neutral-dark group-hover:text-primary transition-colors tracking-tight">
                                                {link.label}
                                            </span>
                                        </Link>
                                    </motion.div>
                                ))}
                            </div>

                            {/* Footer Area */}
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: 0.5 }}
                                className="space-y-8 pt-12 border-t border-neutral-100"
                            >
                                <div className="flex items-center justify-between">
                                    <div className="flex gap-4">
                                        <button
                                            onClick={() => { router.replace(pathname.replace(`/${locale}`, '/en')); setIsMenuOpen(false); }}
                                            className={`font-bold tracking-widest ${locale === 'en' ? 'text-primary' : 'text-neutral-medium'}`}
                                        >
                                            EN
                                        </button>
                                        <span className="text-neutral-200">/</span>
                                        <button
                                            onClick={() => { router.replace(pathname.replace(`/${locale}`, '/fr')); setIsMenuOpen(false); }}
                                            className={`font-bold tracking-widest ${locale === 'fr' ? 'text-primary' : 'text-neutral-medium'}`}
                                        >
                                            FR
                                        </button>
                                    </div>
                                    <div className="flex gap-4">
                                        <a href={siteInfo.social.instagram} className="w-12 h-12 rounded-full bg-neutral-50 flex items-center justify-center">
                                            <Compass className="w-5 h-5" />
                                        </a>
                                    </div>
                                </div>

                                <Link
                                    href={`https://wa.me/${siteInfo.contact.whatsapp.replace(/\+/g, '')}`}
                                    className="block w-full bg-neutral-dark text-white py-6 rounded-[2rem] text-center font-bold text-lg shadow-2xl shadow-black/10 transition-transform active:scale-95"
                                >
                                    Instant WhatsApp Chat
                                </Link>

                                <p className="text-center text-xs text-neutral-400 font-bold uppercase tracking-[0.2em]">
                                    {siteInfo.name} &copy; {new Date().getFullYear()}
                                </p>
                            </motion.div>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </>
    );
}
